%transitionPage {
  transition: all 0.4s;
}

%lightThemeHoverButton {
  border: 1px solid $border-hover-button;
}

%darkThemeHoverButton {
  background-color: $background-hover-button;
}

@mixin space-between-sectionsDesktop {
  margin-bottom: 80px;
}

@mixin space-between-sectionsTablet {
  margin-bottom: 64px;
}

@mixin space-between-sectionsPhone {
  margin-bottom: 56px;
}

@mixin onTablet {
  @media (max-width: $tablet-max-width) {
    @content;
  }
}

@mixin onPhone {
  @media (max-width: $phone-max-width) {
    @content;
  }
}

@mixin onScale {
  &:hover {
    transform: scale(1.1);
  }
}
